import React, { useContext, useState } from 'react'
import ApiContext from '../../../../context/API/apiContext';

const MyProfileUpdate = () => {
    const apiContext = useContext(ApiContext);
    const { logged, updateUserInfo, alert, setAlert } = apiContext;

    const [info, setInfo] = useState({
        firstname: logged.name,
        lastname: logged.lastname,
        age: logged.age,
        city: logged.city
    })

    const [isUpdated, setIsUpdated] = useState(false);

    const { firstname, lastname, age, city } = info;

    const onSubmit = (e) => {
        e.preventDefault();
        if (firstname === '' || lastname === '' || age === '' || city === '') {
            setAlert();
            setTimeout(() => { setAlert() }, 1000);
        }
        else {
            logged.name = firstname;
            logged.lastname = lastname;
            logged.age = age;
            logged.city = city;
            updateUserInfo(logged);
            setInfo({
                firstname: '',
                lastname: '',
                age: '',
                city: ''
            });
            setIsUpdated(true);
            setTimeout(() => { setIsUpdated(false) }, 1000);
        }

    }

    const onChange = (e) => {
        setInfo({ ...info, [e.target.name]: e.target.value });

    }
    return (
        <div className="my-profile-update">
            <div className="row">
                <h2>{`${logged.name}, ${logged.age}`}</h2>
                {isUpdated && <p style={{
                    padding: '.5rem 1rem', color: '#fff', borderRadius: '5px',
                    background: 'green', marginLeft: '2rem', fontSize: '1.4rem'
                }}>User updated</p>}
            </div>
            <div className="row">
                <div className="row-part"><i className="fas fa-camera"></i> Add photos</div>
                <div className="row-part"><i className="fas fa-video"></i> Add video</div>
                <div className="row-part">
                    <img src={logged.img} alt="" />
                </div>
                <div className="row-part"><i className="fas fa-camera"></i></div>
                <div className="row-part"><i className="fas fa-camera"></i></div>
                <div className="row-part"><i className="fas fa-camera"></i></div>
            </div>
            <form onSubmit={onSubmit}>
                <div className="form-container">
                    <div className="form-container-left">
                        <h1>Personal informations</h1>
                        {alert && <p className="p-desc" style={{ color: 'red', textAlign: 'center', marginTop: '1rem', fontSize: '1.5rem' }}>Please fill in all inputs</p>}

                        <div className="input-row">
                            <label htmlFor="firstname-settings" >Firstname</label>
                            <input type="text" placeholder="Firstname" onChange={onChange} value={firstname} name="firstname" />
                        </div>
                        <div className="input-row">
                            <label htmlFor="lastname-settings">Lastname</label>
                            <input type="text" placeholder="Lastname" onChange={onChange} value={lastname} name="lastname" />
                        </div>
                        <div className="input-row">
                            <label htmlFor="age-settings">Age</label>
                            <input type="text" placeholder="Age" onChange={onChange} value={age} name="age" />
                        </div>
                        <div className="input-row">
                            <label htmlFor="city-settings">City</label>
                            <input type="text" placeholder="City" onChange={onChange} value={city} name="city" />
                        </div>

                    </div>
                    <div className="form-container-right">
                        <h1>Privacy settings</h1>
                        <div className="input-row">
                            <label htmlFor="email-settings">Email</label>
                            <input type="text" placeholder="Email" />
                        </div>
                        <div className="input-row">
                            <label htmlFor="password-settings">Password</label>
                            <input type="password" placeholder="Password" />
                        </div>
                        <div className="input-row">
                            <label htmlFor="password2-settings">Password</label>
                            <input type="password" placeholder="Password" />
                        </div>
                    </div>
                </div>
                <input type="submit" value="Submit" />
            </form>
        </div>
    )
}

export default MyProfileUpdate
