import React, { useContext, useState } from 'react'
import ApiContext from './../../../../context/API/apiContext';

const UserProfileDesc = () => {
    const apiContext = useContext(ApiContext);
    const { user, heartUser, logged, declineUser } = apiContext;

    let a = Object.getOwnPropertyNames(logged).length >= 1 ? logged.liked.find(like => like === user.id) : null;

    const [status, setStatus] = useState('');

    const onClickHeart = () => {

        let liked = logged.liked;
        liked = [...logged.liked, user.id];
        logged.liked = liked;
        heartUser(logged);
        setStatus('hearted');
    }

    const onClickDecline = () => {
        let disliked = logged.disliked;
        disliked = [...logged.disliked, user.id];
        logged.disliked = disliked;
        declineUser(logged);
        setStatus('declined');
    }


    return (
        <div className="profile-user" id={user.id}>
            <div className="profile-user-row">
                <div className="link-reactions">
                    {/* Provjera da li je user logovan, i u zavisnosti od toga da li ce prikazat reakcije ili lijevu marginu od imena */}
                    {
                        Object.getOwnPropertyNames(logged).length >= 1 ? (a ? <div className="like"><i className="fas fa-thumbs-up"></i></div> :
                            (status === '' ? <div className="like" onClick={onClickHeart}><i className="fas fa-heart "></i></div> :
                                (status === 'hearted' ?
                                    <div className="like"><i className="fas fa-thumbs-up"></i></div> : ''))) : ''
                    }
                    {
                        Object.getOwnPropertyNames(logged).length >= 1 ? (a ? '' :
                            (status === '' ? <div className="decline" onClick={onClickDecline}> <i className="fas fa-times "></i> </div> :
                                (status === 'declined' ?
                                    <div className="decline" ><i className="fas fa-thumbs-down"></i></div> : ''))) : ''
                    }


                    {Object.getOwnPropertyNames(logged).length >= 1 ? <h2>{`${user.name}, ${user.age}`}</h2> : <h2 style={userStyle}>{`${user.name}, ${user.age}`}</h2>}
                </div>
                {Object.getOwnPropertyNames(logged).length >= 1 &&
                    <div className="link-others">
                        <a href="#!" className="chat-button">
                            Chat now <i className="fas fa-comment-dots"></i></a>
                        <div className="dropdown">
                            <a href="#!" className="dropdown-button">
                                <i className="fas fa-ellipsis-h"></i>
                            </a>
                            <div className="dropdown-menu">
                                <ul>
                                    <li>
                                        <a href="#!"><i className="far fa-share-square"></i> Share</a>
                                    </li>
                                    <li>
                                        <a href="#!"
                                        ><i className="fas fa-flag"></i> Block or report</a >
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                }
            </div>
            <div className="profile-user-container">
                <div className="profile-user-container-left">
                    <div className="box">
                        <h2>Location</h2>
                        <p>{user.city}</p>
                    </div>
                    <div className="box">
                        <h2>I'm here to</h2>
                        <p>Wants to chat</p>
                    </div>
                    <div className="box">
                        <h2>Personal info</h2>
                        <p><span>Firstname</span>: {user.name}</p>
                        <p><span>Lastname</span>: {user.lastname}</p>
                        <p><span>Sex</span>: {user.sex}</p>
                        <p><span>Age</span>: {user.age}</p>
                        <p><span>Email</span>: {user.email}</p>
                    </div>
                    <div className="box box-row">
                        <h2>Social</h2>
                        <div className="link-reactions">
                            <div className="fb">
                                <a href="#!"><i className="fab fa-facebook-f blue"></i></a>
                            </div>
                            <div className="phone">
                                <a href="#!"> <i className="fas fa-mobile-alt green"></i></a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="profile-user-container-right">
                    <img src={user.img} alt="profile" />
                </div>
            </div>
        </div>
    )
}

const userStyle = {
    marginLeft: '1rem'
}


export default UserProfileDesc
