import {
    SEARCH_USERS,
    SET_USER,
    SET_LOGGED,
    REMOVE_LOGGED,
    SEARCH_USER_BY_ID,
    SEARCH_USERS_BY_ID,
    HEART_USER,
    SEARCH_USERS_BY_PARAMETERS,
    SET_UPDATE,
    REGISTER_USER,
    SET_LOADING,
    CLEAR_ALL_USERS,
    SET_ALERT,
    DECLINE_USER,
    SET_MATCHED,
    SET_LIKED_YOU,
    UPDATE_USER_INFO,
    GET_FIRST_USER
} from '../types';

export default (state, action) => {
    switch (action.type) {
        //Pretraga svih korisnika
        case SEARCH_USERS:
            return {
                ...state,
                users: action.payload,
                loading: false
            }
        //Postavljanje usera kad kliknemo na profil npr
        case SET_USER:
            return {
                ...state,
                user: state.users.find(u => (u.id === action.payload))
            }
        //Postavljanje logovanog usera
        case SET_LOGGED:
            return {
                ...state,
                logged: action.payload
            }
        //Uklanjanje logovanog usera
        case REMOVE_LOGGED:
            return {
                ...state,
                logged: {}
            }
        //Pretraga usera po ID
        case SEARCH_USER_BY_ID:
            return {
                ...state,
                users: state.users.filter(user => (user.id !== action.payload))
            }
        //Vraca sve usere koji ne sadrze poslani ID
        case SEARCH_USERS_BY_ID:
            return {
                ...state,
                users: state.users.filter(user => !action.payload.includes(user.id))
            }
        //Pretraga usera po parametrima
        case SEARCH_USERS_BY_PARAMETERS:
            return {
                ...state,
                users: state.users.filter(user => (user.sex === action.payload.gender &&
                    (user.city === action.payload.where[0] || action.payload.where[0] === '' || action.payload.where === '') &&
                    (action.payload.min[0] <= user.age && action.payload.max[0] >= user.age || action.payload.min[0] === ''
                        || action.payload.min === '' || action.payload.max[0] === '' || action.payload.max === '')
                ))
            }
        //Mijenjanje updatea sa false na true i obrnuto
        case SET_UPDATE:
            return {
                ...state,
                updateSettings: !state.updateSettings
            }
        //Registracija korisnika
        case REGISTER_USER:
            return {
                ...state,
                users: [...state.users, action.payload]
            }
        //Setovanje spinner loadinga
        case SET_LOADING:
            return {
                ...state,
                loading: true
            }
        //Brisanje svih korisnika iz statea
        case CLEAR_ALL_USERS:
            return {
                ...state,
                users: []
            }
        //Postavljanje alerta u sekciji sign up
        case SET_ALERT:
            return {
                ...state,
                alert: !state.alert
            }
        //Kada lajkamo korisnika
        case HEART_USER:
            return {
                ...state,
                users: [...state.users, action.payload]
            }
        //kada odbijemo korisnika
        case DECLINE_USER:
            return {
                ...state,
                users: [...state.users, action.payload]
            }
        //Postavljanje uparenih korisnika, koji su se medjusobno lajkali
        case SET_MATCHED:
            return {
                ...state,
                matched: state.users.filter(user => user.liked.includes(action.payload.id) && action.payload.liked.includes(user.id))
            }
        //Postavljanje korisnika koji su vas lajkali
        case SET_LIKED_YOU:
            return {
                ...state,
                likedYou: state.users.filter(user => user.liked.includes(action.payload))
            }
        case UPDATE_USER_INFO:
            return {
                ...state,
                users: [...state.users, action.payload]
            }
        case GET_FIRST_USER:
            return {
                ...state,
                user: state.users ? state.users[0] : {}
            }

        default: return state;
    }
}