import React from 'react'

const MessagesUser = ({ user }) => {
    return (
        <div className="user-message">
            <div className="user-message-img"><img src={user.img} alt="" /></div>
            <div className="user-message-desc">
                <h2>{user.name}</h2>
                <p>{`You matched with ${user.name}`}</p>
            </div>
        </div>
    )
}

export default MessagesUser
