import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import ApiContext from '../../context/API/apiContext';
import { withRouter } from 'react-router-dom';

const NavbarGuest = (props) => {
    const apiContext = useContext(ApiContext);
    const { searchUsers, users, setLoggedUser } = apiContext;

    useEffect(() => {
        searchUsers();

    }, []);

    const [info, setInfo] = useState({
        email: '',
        pw: ''
    });
    const { email, pw } = info;

    const onChange = (e) => {
        setInfo({
            ...info, [e.target.name]: [e.target.value]
        });

    }
    const signIn = (e) => {

        if (email === '' || pw === '') {
            console.error('Invalid input');

        }
        else {
            const searchedUser = users.find(user => (user.email === email[0] && user.password === pw[0]));
            if (searchedUser) {
                setLoggedUser(searchedUser);
                props.history.push('/people-nearby')
            }
        }
    }

    return (
        <nav className="navigation-guest">
            <div className="container-nav">
                <div className="logo"><h1 className="heading-logo">Vadoo</h1></div>
                <div className="main-description">
                    <p>Sign in with email</p>
                    <input type="text" placeholder="Email" name="email" value={email} onChange={onChange} onKeyPress={event => {
                        if (event.key === 'Enter') {
                            signIn();
                        }
                    }} />
                    <input type="password" placeholder="Password" name="pw" value={pw} onChange={onChange} onKeyPress={event => {
                        if (event.key === 'Enter') {
                            signIn();
                        }
                    }} />
                    <a href="#!" className="button-dark" onClick={signIn}> Sign me in!</a>
                    <a href="#!" className="forgot-password">Forgot your password?</a>
                    <p>
                        Not a member? <Link to="/sign-up" className="create-acc2">Create account</Link>
                    </p>
                </div>
            </div>
        </nav>
    )
}

export default withRouter(NavbarGuest)
