import React, { useContext, useEffect } from 'react'
import User from '../People Nearby/User';
import ApiContext from './../../../../context/API/apiContext';

const UserGridMatched = () => {
    const apiContext = useContext(ApiContext);

    const { searchUserByID, matched, logged, setMatched, searchUsers } = apiContext;

    const searchMatchedUsers = async () => {
        await searchUsers();
        searchUserByID(logged.id);
        setMatched(logged);
    }

    useEffect(() => {
        searchMatchedUsers();

    }, [])


    return (
        <div className="users-grid">
            {matched.map(user => (<User key={user.id} user={user} />))}
        </div>
    )
}

export default UserGridMatched
