import React from 'react'
import { Link } from 'react-router-dom'
const NavbarMain = () => {
    return (
        <nav className="navigation-main">
            <div className="logo"><h1 className="heading-logo">Vadoo</h1></div>
            <div className="links-group">
                <div className="meetings">
                    <Link to="/encounters"><i className="fas fa-user-friends"></i> Encounters</Link >
                </div>
                <div className="people-nearby">
                    <Link to="/people-nearby-sign-in"><i className="fas fa-globe-europe"></i> People nearby</Link >
                </div>
                <Link to="/sign-up" className="create-acc">Create account</Link>
            </div>
        </nav>
    )
}

export default NavbarMain
