import React, { useContext, useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom'
import Modal from './SignOutModal/LogOutModal';
import useModal from './SignOutModal/useModal';

import ApiContext from '../../../context/API/apiContext';
import { withRouter } from 'react-router-dom';

const NavbarLogged = (props, { match }) => {

    const apiContext = useContext(ApiContext);
    const { logged, matched } = apiContext;
    const { isShowing, toggle, signOutModal } = useModal();
    const [showNavbar, setShowNavbar] = useState(false);
    useEffect(() => {
        if (Object.getOwnPropertyNames(logged).length === 0) {
            props.history.push('/');
        }
    })
    const onClick = () => {

        toggle();
    }

    const openMyProfile = () => {
        props.history.push('/myprofile')
    }

    const showNavbarMobile = (e) => {
        setShowNavbar(!showNavbar);
        // console.log(showNavbar);
        if (showNavbar) {
            e.target.parentElement.nextElementSibling.style.display = 'block';
        }
        else {
            e.target.parentElement.nextElementSibling.style.display = 'none';
        }

    }
    return (
        <nav className="navigation-logged">
            <div className="container-nav">
                <div className="logo"><h1 className="heading-logo">Vadoo</h1></div>
                <div className="main-description">
                    <div className="profile-main-info">
                        <div className="profile-main-info-img" onClick={openMyProfile}>
                            <img src={logged.img} alt="profile" />
                            <div className="overlay"></div>
                        </div>
                        <div className="profile-main-info-desc">
                            <h3>{logged.name}</h3>
                            {matched.length < 4 ? <p>Popularity: <span className="popularity-low">Very low</span></p> :
                                <p>Popularity: <span style={high}>Very high</span></p>}
                        </div>
                        <div className="profile-main-info-logout">
                            <a href="#!" onClick={onClick}> <i className="fas fa-sign-out-alt"></i></a>
                        </div>
                    </div>
                    <a href="#!" className="button-dark"
                    ><i className="fas fa-bolt"></i> Increase Popularity</a>
                </div>
                <div className="profile-links">
                    <div className="navbar-mobile-bar" onClick={showNavbarMobile} value={showNavbar}><i className="fas fa-bars"></i></div>
                    <ul className="list">
                        <li className="list-item"><NavLink to='/encounters' activeClassName="active">Encounters</NavLink></li>
                        <li className="list-item"><NavLink to='/people-nearby' activeClassName="active">People nearby</NavLink></li>
                        <li className="list-item"><NavLink to='/live' activeClassName="active">Live</NavLink></li>
                        <li className="list-item"><NavLink to="/messages" activeClassName="active">Messages</NavLink></li>
                        <li className="list-item"><NavLink to='/matched' activeClassName="active">Matched</NavLink></li>
                        <li className="list-item"><NavLink to="/liked-you" activeClassName="active">Liked you</NavLink></li>
                    </ul>
                </div>
            </div>
            <Modal
                isShowing={isShowing}
                hide={toggle}
                signOutModal={signOutModal}
            />
        </nav>
    )
}

const high = {
    color: '#28A21B'
}
export default withRouter(NavbarLogged)
