import React, { useContext, useEffect, useState } from 'react'
import ApiContext from '../../../../context/API/apiContext';
const Encounter = () => {
    const apiContext = useContext(ApiContext);
    const { logged, heartUser, declineUser, user, getFirstUser, users, setUser } = apiContext;
    // const [encounter, setEncounter] = useState(0);
    useEffect(() => {

        let curr = users[0];
        setUser(curr.id);

    }, [])

    const onClickHeart = () => {

        let liked = logged.liked;
        liked = [...logged.liked, user.id];
        logged.liked = liked;
        heartUser(logged);

    }

    const onClickDecline = () => {
        let disliked = logged.disliked;
        disliked = [...logged.disliked, user.id];
        logged.disliked = disliked;
        declineUser(logged);
    }

    return (
        <div className="encounter">
            <div className="encounter-img">
                <img src={user.img} alt="" />
                <div className="link-reactions">
                    {Object.getOwnPropertyNames(logged).length >= 1 ?
                        <div className="like" onClick={onClickHeart}> <i className="fas fa-heart "></i> </div> :
                        <div className="like" ><i className="fas fa-heart "></i> </div>}
                    {Object.getOwnPropertyNames(logged).length >= 1 ?
                        <div className="decline" onClick={onClickDecline}> <i className="fas fa-times "></i> </div> :
                        <div className="decline" ><i className="fas fa-times "></i> </div>}

                </div>
            </div>
            <div className="encounter-description">
                <h1><i className="fas fa-circle"></i> {`${user.name}, ${user.age}`}</h1>
                <h2>Personal info</h2>
                <p>Relationship: I'm single</p>
                <p>Sexuality: I'm straight</p>
            </div>
        </div>
    )
}

export default Encounter
