import React, { useState, useContext, useEffect } from 'react'
import ApiContext from '../../../context/API/apiContext';
import { withRouter } from 'react-router-dom';

const SignInModel = (props) => {

    const apiContext = useContext(ApiContext);

    useEffect(() => {
        searchUsers();
    }, []);

    const { searchUsers, users, setLoggedUser } = apiContext;

    const [info, setInfo] = useState({
        email: '',
        pw: ''
    });
    const [alertInput, setAlertinput] = useState(false);
    const { email, pw } = info;

    const onChange = (e) => {
        setInfo({
            ...info, [e.target.name]: [e.target.value]
        });

    }
    const signIn = (e) => {

        if (email === '' || pw === '') {
            // console.error('Invalid input');
            setAlertinput(true);
            setTimeout(() => setAlertinput(false), 3000);
        }
        else {
            const searchedUser = users.find(user => (user.email === email[0] && user.password === pw[0]));
            if (searchedUser) {
                setLoggedUser(searchedUser);
                props.history.push('/people-nearby')
            }
        }
    }
    return (
        <div className="sign-in-model">
            <div className="left-side">
                <h1 className="heading-counter">88,566</h1>
                <p className="paragraph-counter-desc">
                    people have already joined,<br />
                    jump in!
          </p>
                <a href="#!" className="sign-in"><i className="fab fa-facebook-square"></i> Sign in via Facebook</a>
                <p className="paragraph-small">We never post on your behalf.</p>
            </div>
            <div className="right-side">
                <h1 className="heading-sign-in">Sign in to Vadoo</h1>
                <input
                    type="text"
                    placeholder="Email or phone number"
                    name="email"
                    className="input-sign-in"
                    id="input-sign-in-email"
                    value={email}
                    onChange={onChange}
                    onKeyPress={event => {
                        if (event.key === 'Enter') {
                            signIn();
                        }
                    }}
                />
                <input type="password"
                    placeholder="Password"
                    name="pw"
                    className="input-sign-in"
                    id="input-sign-in-pw"
                    value={pw}
                    onChange={onChange}
                    onKeyPress={event => {
                        if (event.key === 'Enter') {
                            signIn();
                        }
                    }}
                />


                <div className="remember-check">
                    <input type="checkbox" name="check" id="check" />
                    <label htmlFor="check"> Remember me</label>
                    {alertInput && <p className="p-desc" style={{
                        color: '#fff', fontSize: '1.3rem', background: 'red', borderRadius: '5px', padding: '.1rem 1rem', marginLeft: '2rem'
                    }}>Invalid input</p>}
                </div>
                <a href="#!" className="sign-in" onClick={signIn}>Sign me in!</a>
                <a href="#!" className="forgot-pass">Forgot yout password?</a>
            </div>
        </div>
    )
}


export default withRouter(SignInModel)
