import React from 'react'
import { Link } from 'react-router-dom'

const NavbarSignUp = () => {
    return (
        <nav className="navigation-sign-up">
            <div className="container">
                <div className="logo"><h1 className="heading-logo">Vadoo</h1></div>
                <div className="links-group"> Already a member?
                <Link to="/" className="sign-in-button">Sign in</Link>
                </div>
            </div>
        </nav>
    )
}

export default NavbarSignUp
