import React, { Fragment } from 'react'

const Camera = () => {
    return (
        <Fragment>
            <div className="upper-main-grid">
                <div className="options">
                    <h1>Live</h1>
                </div>
            </div>
            <div className="go-live">
                <i className="fas fa-video"></i>
                <a href="#!" className="go-live-button">Go live</a>
            </div>
        </Fragment>
    )
}

export default Camera
