import React, { useState, useContext } from 'react'
import uuid from 'uuid';
import ApiContext from '../../../context/API/apiContext';

const SignUpForm = () => {
    const apiContext = useContext(ApiContext);
    const { registerUser, setAlert, alert } = apiContext;

    const [informations, setInformations] = useState({
        name2: '',
        age2: '',
        city2: '',
        email2: '',
        password2: ''
    });
    const { name2, age2, city2, email2, password2 } = informations;
    const onChange = (e) => {
        setInformations({
            ...informations, [e.target.name]: [e.target.value]
        });
    }
    const [gender, setGender] = useState('male');
    const setClickGender = (e) => {
        if (e.target.id === 'male') {
            setGender('male');
            e.target.className = 'gender-option active';
            e.target.nextSibling.className = 'gender-option';
        }
        else {
            setGender('female');
            e.target.className = 'gender-option active';
            e.target.previousSibling.className = 'gender-option';
        }
    }

    const sendUserInfo = () => {
        //Lose, mora ovaj dio bolje...
        const id = uuid.v4();
        let name, age, city, email, password, sex;
        name = name2.toString();
        age = age2.toString();
        city = city2.toString();
        email = email2.toString();
        password = password2.toString();
        sex = gender;
        const lastname = '', img = 'http://www.sheltia.com/media/1230/defaultuserimage.jpg', liked = [], disliked = [];
        let obj = {
            id,
            name,
            lastname,
            age,
            sex,
            password,
            email,
            city,
            img,
            liked,
            disliked
        }

        //Validacija katastrofaaaaaa
        if (name.length >= 3 && age >= 18 && city.length >= 3 && password.length >= 5) {
            registerUser(obj);
            // console.log(obj);
        }
        else {
            setAlert();
            setTimeout(() => { setAlert() }, 3000);
        }
    }

    return (
        <section className="section-sign-up">
            <div className="container">
                <div className="sign-up-form">
                    <h1>Create new account</h1>
                    <p className="p-desc">
                        Join more than <span>80 thousand new Vadoo friends.</span>
                    </p>
                    {alert && <p className="p-desc" style={{ color: 'red', textAlign: 'center', marginTop: '1rem' }}>Please fill in all inputs</p>}
                    <div className="input-group">
                        <label htmlFor="name-sign-up">Name</label>
                        <input
                            type="text"
                            placeholder="Your first name"
                            id="name-sign-up"
                            name="name2"
                            onChange={onChange}
                        />
                    </div>
                    <div className="input-group">
                        <label htmlFor="birthday-sign-up">How old are you?</label>
                        <input
                            type="text"
                            placeholder="How old are you?"
                            id="birthday-sign-up"
                            name="age2"
                            onChange={onChange}
                        />
                    </div>
                    <div className="input-group">
                        <label htmlFor="city-sign-up">Your city</label>
                        <input
                            type="text"
                            placeholder="Type your location"
                            id="city-sign-up"
                            name="city2"
                            onChange={onChange}
                        />
                    </div>
                    <div className="input-group">
                        <label htmlFor="name-sign-up">Gender</label>
                        <div className="gender-options">
                            <a href="#!" className="gender-option" id="male" onClick={setClickGender}
                            ><i className="fas fa-male"></i> Male</a >
                            <a href="#!" className="gender-option" id="female" onClick={setClickGender}
                            ><i className="fas fa-female"></i> Female</a >
                        </div>
                    </div>
                    <div className="input-group">
                        <label htmlFor="email-sign-up">Email</label>
                        <input type="text"
                            placeholder="Email"
                            id="email-sign-up"
                            name="email2"
                            onChange={onChange}
                        />
                    </div>
                    <div className="input-group">
                        <label htmlFor="password-sign-up">Password</label>
                        <input
                            type="password"
                            placeholder="Create your password"
                            id="password-sign-up"
                            name="password2"
                            onChange={onChange}
                        />
                        <p className="input-desc">Password must be at least 5 characters</p>
                    </div>
                    <a href="#!" className="sign-up-button" onClick={sendUserInfo}>Sign up</a>
                    <div className="privacy-policy">
                        <p>
                            By continuing, you're confirming that you've read and agree<br />
                            to our <a href="#!">Terms and Conditions</a>,<a href="#!"> Privacy Policy</a> and <a href="#!">Cookie Policy</a>
                        </p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default SignUpForm
