import React, { useContext } from 'react'
import NavbarLogged from './../NavbarLogged';
import Footer from './../../Footer';
import MyProfileDesc from './UserProfileDesc';
import NavbarGuest from '../../NavbarGuest';
import ApiContext from './../../../../context/API/apiContext';
import { Helmet } from 'react-helmet';

const UserProfile = () => {
    const apiContext = useContext(ApiContext);
    const { logged, user } = apiContext;

    return (
        <div className="container container-split">
            <Helmet>
                <title>{`Vadoo | ${user.name}, ${user.age}`}</title>
                <meta charSet="utf-8" />
                <meta name="description" content="A React.js application" />
            </Helmet>
            {Object.getOwnPropertyNames(logged).length >= 1 ? <NavbarLogged /> : <NavbarGuest />}
            <div className="container-main">
                <MyProfileDesc />
                <Footer />
            </div>
        </div>
    )
}

export default UserProfile
