import React, { useContext } from 'react'
import MessagesUser from './MessagesUser';
import ApiContext from '../../../../context/API/apiContext';

const MessagesGrid = () => {
    const apiContext = useContext(ApiContext);
    const { matched } = apiContext;
    return (
        <div className="messages-container">
            <div className="messages-container-left">
                {matched.map(user => <MessagesUser key={user.id} user={user} />)}
            </div>
            <div className="messages-container-right">
                <i className="fas fa-envelope"></i>
            </div>
        </div>
    )
}

export default MessagesGrid
