import React, { useReducer } from 'react';
import axios from 'axios';
import ApiContext from './apiContext';
import ApiReduceer from './apiReducer';

import {
    SEARCH_USERS,
    SET_USER,
    SET_LOGGED,
    REMOVE_LOGGED,
    SEARCH_USER_BY_ID,
    SEARCH_USERS_BY_ID,
    HEART_USER,
    SEARCH_USERS_BY_PARAMETERS,
    SET_UPDATE,
    REGISTER_USER,
    SET_LOADING,
    CLEAR_ALL_USERS,
    SET_ALERT,
    DECLINE_USER,
    SET_MATCHED,
    SET_LIKED_YOU,
    UPDATE_USER_INFO,
    GET_FIRST_USER
} from '../types';


const ApiState = props => {
    const initialState = {
        users: [],
        user: {},
        loading: false,
        logged: {},
        updateSettings: false,
        alert: false,
        matched: [],
        likedYou: []
    }

    const [state, dispatch] = useReducer(ApiReduceer, initialState);

    //Metode...

    const searchUsers = async () => {
        setLoading();
        const res = await axios.get('/users');
        // console.log(res.data);

        dispatch({
            type: SEARCH_USERS,
            payload: res.data
        })
    }


    const setUser = (ID) => {

        dispatch({
            type: SET_USER,
            payload: ID
        })
    }

    const setLoggedUser = (user) => {
        dispatch({
            type: SET_LOGGED,
            payload: user
        })
    }

    const signOut = () => {
        dispatch({
            type: REMOVE_LOGGED
        })
    }

    const searchUserByID = (ID) => {
        dispatch({
            type: SEARCH_USER_BY_ID,
            payload: ID
        });
    }

    const searchUsersByID = (liked) => {
        dispatch({
            type: SEARCH_USERS_BY_ID,
            payload: liked
        });
    }

    const searchUsersByParameters = (parameters) => {
        dispatch({
            type: SEARCH_USERS_BY_PARAMETERS,
            payload: parameters
        })
    }

    const setUpdateSettings = () => {
        dispatch({
            type: SET_UPDATE
        })
    }

    const registerUser = async (info) => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        const res = await axios.post('/users', info, config);

        dispatch({
            type: REGISTER_USER,
            payload: res.data
        })
    }

    const setLoading = () => dispatch({ type: SET_LOADING });

    const clearAllUsers = () => dispatch({ type: CLEAR_ALL_USERS });

    const setAlert = () => {
        dispatch({
            type: SET_ALERT
        })
    }
    const heartUser = async (obj) => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        const res = await axios.put(`/users/${state.logged.id}`, obj, config);
        dispatch({
            type: HEART_USER,
            payload: res.data
        });
        // console.log(res.data);
    }

    const declineUser = async (obj) => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        const res = await axios.put(`/users/${state.logged.id}`, obj, config);
        dispatch({
            type: DECLINE_USER,
            payload: res.data
        });
    }

    const setMatched = (obj) => {

        dispatch({
            type: SET_MATCHED,
            payload: obj
        })
    }

    const setLikedYou = (ID) => {
        dispatch({
            type: SET_LIKED_YOU,
            payload: ID
        })
    }

    const updateUserInfo = async (obj) => {
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        const res = await axios.put(`/users/${state.logged.id}`, obj, config);
        dispatch({
            type: UPDATE_USER_INFO,
            payload: res.data
        });
    }

    const getFirstUser = () => {
        dispatch({
            type: GET_FIRST_USER
        })
    }

    return <ApiContext.Provider
        value={{
            users: state.users,
            user: state.user,
            loading: state.loading,
            logged: state.logged,
            searchUsers,
            setUser,
            setLoggedUser,
            signOut,
            searchUserByID,
            searchUsersByID,
            searchUsersByParameters,
            setUpdateSettings,
            updateSettings: state.updateSettings,
            registerUser,
            setLoading,
            clearAllUsers,
            alert: state.alert,
            setAlert,
            heartUser,
            declineUser,
            matched: state.matched,
            setMatched,
            likedYou: state.likedYou,
            setLikedYou,
            updateUserInfo,
            getFirstUser
        }}>
        {props.children}
    </ApiContext.Provider>
}

export default ApiState;