import React from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import SignIn from './components/layout/SignIn/SignIn';
import SignUp from './components/layout/SignUp/SignUp';
import PeopleNearby from './components/layout/Logged/People Nearby/PeopleNearby';
import PeopleNearbyGuest from './components/layout/Logged/People Nearby/PeopleNearbyGuest';
import Encounters from './components/layout/Logged/Encounters/Encounters';
import Live from './components/layout/Logged/Live/Live';
import Matched from './components/layout/Logged/Matched/Matched';
import MyProfile from './components/layout/Logged/MyProfile/MyProfile';
import UserProfile from './components/layout/Logged/UserProfile/UserProfile';
import LikedYou from './components/layout/Logged/Liked You/LikedYou';
import Messages from './components/layout/Logged/Messages/Messages';
import NotFound from './components/layout/NotFound';


import ApiState from './context/API/ApiState';
const App = () => {
  return (
    <ApiState>
      <Router basename='/'>
        <div className="App">
          <Switch>
            <Route exact path='/' component={SignIn} />
            <Route exact path='/sign-up' component={SignUp} />
            <Route exact path='/people-nearby' component={PeopleNearby} />
            <Route exact path='/encounters' component={Encounters} />
            <Route exact path='/live' component={Live} />
            <Route exact path='/matched' component={Matched} />
            <Route exact path='/myprofile' component={MyProfile} />
            <Route exact path='/users/:login' component={UserProfile} />
            <Route exact path='/people-nearby-sign-in' component={PeopleNearbyGuest} />
            <Route exact path='/liked-you' component={LikedYou} />
            <Route exact path='/messages' component={Messages} />
            <Route component={NotFound} />
          </Switch>
        </div>
      </Router>
    </ApiState>
  );
}

export default App;
