import React from 'react';
import ReactDOM from 'react-dom';

const LogOutModal = ({ isShowing, hide, signOutModal }) => isShowing ? ReactDOM.createPortal(

    <React.Fragment>
        <div className="sign-out-modal">
            <h1>Do you really want to sign out?</h1>
            <div className="row">
                <a href="#!" className="sign-out-yes-modal" onClick={signOutModal}>Yes</a>
                <a href="#!" className="sign-out-no-modal" onClick={hide}>No</a>
            </div>
        </div>
    </React.Fragment>, document.body
) : null;


export default LogOutModal


